body {
  background-color: #aeaeae;
}

.form {
  max-width: 400px;
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  margin: auto;
  padding: 20px;
  display: flex;
}

.form > label {
  display: block;
}

.form > button {
  text-align: center;
  cursor: pointer;
  border-width: .5px;
  border-radius: 5px;
  padding: 5px 30px;
  font-size: 20px;
  text-decoration: none;
  display: inline-block;
}

